<template>
  <div>
    <div class="d-block header-section">
      <div class="">
        <h1>
          <b>Return Orders</b>
        </h1>
      </div>
    </div>
    <div class="container-fluid productManagement">
      <!-- Your Details Model -->
      <b-modal id="deactiveItem" hide-footer hide-header centered>
        <div class="d-block">
          <div class="modal-body mt-4 mb-3" align="center">
            <div class="mt-4 mb-4">
              <img src="@/assets/images/icons/warning-message-icon.png" />
            </div>
          </div>
        </div>
      </b-modal>
      <!-- Your Details Model End-->
      <!-- START: Card Data-->
      <div class="row mb-5">
        <div class="w-100">
          <div class="row">
            <div class="col-12 col-sm-12">
              <div class="search-tbl">
                <div class="form-row col-md-12 p-3">
                  <div class="form-group row col-md-6 mr-2">
                    <label class="col-form-label mr-2 ml-3">Package Type</label>
                    <div class="col-md-4">
                      <select class="form-control" v-model="selectedPackage">
                        <option v-for="opt in packageType" :key="opt">
                          {{ opt }}
                        </option>
                      </select>
                    </div>

                    <label for="username" class="col-form-label mr-2"
                      >Code</label
                    >
                    <div class="col-md-4">
                      <input
                        type="text"
                        class="form-control"
                        v-model="reelCartonCode"
                        placeholder
                      />
                    </div>
                  </div>
                  <div class="col-md-6">
                    <button
                      type="button"
                      class="btn btn-primary float-right p-2 pl-5 pr-5"
                      @click="getCodeDetails"
                    >
                      ADD
                    </button>
                  </div>
                </div>
              </div>
            </div>

            <div class="col-12 col-sm-12 mt-3">
              <div class="table-responsive">
                <table
                  class="table layout-secondary return-order bordered"
                  v-if="detailsList.length"
                >
                  <thead>
                    <tr>
                      <th>Package Type</th>
                      <th>Product Type</th>
                      <th>No. of Reals</th>
                      <th>Real Size</th>
                      <th></th>
                    </tr>
                  </thead>

                  <tbody v-for="listItem in detailsList" :key="listItem">
                    <tr>
                      <td>{{ selectedPackage }}</td>
                      <td class="ImporDome">{{ listItem.productName }}</td>
                      <td>{{ listItem.reelCount || listItem.stampCount }}</td>
                      <td>
                        {{
                          listItem.reels
                            ? listItem.reels[0].reelSize
                            : listItem.reelSize
                        }}
                      </td>
                      <td>
                        <img src="@/assets/images/icons/cross-small.png" />
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
          <div class="row">
            <div class="form-group  col-md-3">
              <label class="col-form-label"><b>Shippers</b></label>
              <div class="">
                <select class="form-control" v-model="selectedShipper">
                  <option
                    v-for="(value, name) in shipperList"
                    :key="value"
                    :value="{ shipperId: parseInt(name), shipperName: value }"
                  >
                    {{ value }}
                  </option>
                </select>
              </div>
            </div>
            <div class="form-group  col-md-3">
              <label class="col-form-label"><b>Delivery Partner</b></label>
              <div class="">
                <select class="form-control" v-model="selectedDeliveryParner">
                  <option
                    v-for="(value, name) in deliveryParnetList"
                    :key="value"
                    :value="{
                      warehouseId: parseInt(name),
                      warehouseName: value,
                    }"
                  >
                    {{ value }}
                  </option>
                </select>
              </div>
            </div>
            
          </div>
          <div class="row">
            <div class="col-md-3">
              <p class="">
                <label for="expectedDate" class="mb-0 text-dark"
                  >Expected Date</label
                >
              </p>
              <div class="">
                <div class="">
                  <b-input-group>
                    <b-form-input
                      id="expectedDate"
                      v-model="expectedDate"
                      type="text"
                      autocomplete="off"
                    ></b-form-input>
                    <b-input-group-append>
                      <b-form-datepicker
                        v-model="expectedDate"
                        button-only
                        right
                        locale="en-US"
                        button-variant="primary"
                      ></b-form-datepicker>
                    </b-input-group-append>
                  </b-input-group>
                </div>
              </div>
            </div>
            <div class="col-md-3">
              <div class="form-group">
                <label class="col-form-label"><b>Tracking ID</b></label>
                <div class="">
                  <input name="trackingID" v-model="trackingId" class="form-control" />
                </div>
              </div>
            </div>
          </div>
          <div class="row">
            <div class="col-md-6">
              <div class="form-group">
                <label class="col-form-label mr-2"><b>Comment</b></label>
                <div class="">
                  <textarea
                    class="form-control"
                    rows="5"
                    v-model="comment"
                  ></textarea>
                </div>
              </div>
            </div>
          </div>
          <div class="col-md-12 float-right">
            <button
              type="button"
              class="btn btn-primary float-right mt-3 mb-5 p-2 pl-5 pr-5 "
              @click="createReturnOrder"
              :disabled="submitAllowed"
            >
              SUBMIT
            </button>
            <button
              type="button"
              class="btn btn-outline-primary float-right mt-3 mb-5 p-2 pl-5 pr-5 mr-4"
            >
              CANCEL
            </button>
          </div>
        </div>
      </div>
      <!-- END: Card DATA-->
    </div>
  </div>
</template>

<script>
import {
  getCartonOrReelDetails,
  getShippers,
  getWarehouses,
  CreateReturnApi,
  getOrderReturns,
} from "../../services/orderMgmntApis";
export default {
  data() {
    return {
      selectedPackage: "",
      reelCartonCode: "",
      packageType: [],
      detailsList: [],
      expectedDate: null,
      deliveryParnetList: {},
      selectedDeliveryParner: "",
      shipperList: {},
      selectedShipper: {},
      reelStore: [],
      cartonStore: [],
      comment: "",
      trackingId: "",
      packageDetailsForPost: [],
    };
  },
  created() {
    this.getShipperAndWarehouse();
  },
  methods: {
    getCodeDetails() {
      let makeApiCall = false;
      if (this.selectedPackage == "Carton") {
        if (this.cartonStore.includes(this.reelCartonCode)) {
          alert("Carton already added");
        } else {
          this.cartonStore.push(this.reelCartonCode);
          makeApiCall = true;
        }
      } else {
        if (this.reelStore.includes(this.reelCartonCode)) {
          alert("Reel already added");
        } else {
          this.reelStore.push(this.reelCartonCode);
          makeApiCall = true;
        }
      }
      if (makeApiCall) {
        getCartonOrReelDetails(this.selectedPackage, this.reelCartonCode)
          .then((result) => {
            if ("status" in result && result.status == 200) {
              if (this.selectedPackage == "Carton") {
                this.reelStore = [
                  ...result.data.reels.map((reel) => reel.code),
                  this.reelStore,
                ];
              }
              this.detailsList.push(result.data);
              let tempResult = result.data;
              if (this.selectedPackage == "Carton") {
                this.packageDetailsForPost.push({
                  packageId: tempResult.id,
                  code: tempResult.code,
                  packageType: tempResult.packageType,
                  reelCount: tempResult.reelCount,
                });
              } else {
                this.packageDetailsForPost.push({
                  packageId: tempResult.id,
                  code: tempResult.code,
                  stampCount: tempResult.stampCount,
                  packageType: tempResult.packageType,
                });
              }
            }
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    getShipperAndWarehouse() {
      getShippers()
        .then((result) => {
          if ("status" in result && result.status == 200) {
            this.shipperList = result.data;
          }
        })
        .catch((err) => {
          console.log(err);
        });
      getWarehouses()
        .then((result) => {
          if ("status" in result && result.status == 200) {
            this.deliveryParnetList = result.data;
          }
        })
        .catch((err) => {
          console.log(err);
        });

        getOrderReturns()
        .then((result) => {
          if ("status" in result && result.status == 200) {
            this.packageType = Object.values(result.data).filter(item => item !=='Pallet');            
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    createReturnOrder() {
      let postData = {
        packageDtos: [...this.packageDetailsForPost],
        trackingId: this.trackingId,
        shipper: this.selectedShipper,
        warehouse: this.selectedDeliveryParner,
        expectedDeliveryDate: this.expectedDate + "T13:20:00.207Z",
        comment: this.comment,
      };
      CreateReturnApi(postData)
        .then(() => {
          console.log("return order created");
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  components: {},
  computed: {
    submitAllowed: function() {
      return this.packageDetailsForPost.length &&
        this.selectedShipper &&
        this.selectedDeliveryParner
        ? false
        : true;
    },
  },
};
</script>
