import axios from "axios"
const _apiBaseURLforOrderMgmt = process.env.VUE_APP_OrderMgmtApisBaseUrl;
import store from "../store"


const _apiRequestHeader = { headers: { "Content-Type": "application/json", Authorization: "Bearer " + store.state.clientAppToken} };

function getOrderReturns () {
  return axios.get(`${_apiBaseURLforOrderMgmt}/Packages/GetPackageTypes/`, {
    ..._apiRequestHeader     
  })
  .then( response => response)
  .catch( error => error) 
}
function getCartonOrReelDetails (seletedpackage,code) {
  let apiCartonOrReel ='GetReelDetailsForOrderReturn', paramKey = 'reelCode'
  if (seletedpackage === 'Carton') {
    paramKey = 'cartonCode'
    apiCartonOrReel = 'GetCartonDetailsForOrderReturn'
  }
    return axios.get(`${_apiBaseURLforOrderMgmt}/Packages/${apiCartonOrReel}`, {
        params: {
          [paramKey]: code
        },
        ..._apiRequestHeader     
      })
      .then( response => response)
      .catch( error => error)
}

function getOrderListingApi (listingData) {
    return axios.post(`${_apiBaseURLforOrderMgmt}/ReturnOrders/GetReturnOrders`,{
        ...listingData
      },
{      ..._apiRequestHeader
}     )
      .then( response => response)
      .catch( error => error)
}

function getdownloadApi (orderNo) {
  return axios.get(`${_apiBaseURLforOrderMgmt}/ReturnOrders/DownloadFile`, {
    params: {
      'returnOrderNumber': orderNo
    },
    ..._apiRequestHeader     
  })
  .then( response => response)
  .catch( error => error) 
}

function getShippers () {
  return axios.get(`${_apiBaseURLforOrderMgmt}/Meta/GetShippers`, {
    ..._apiRequestHeader     
  })
  .then( response => response)
  .catch( error => error) 
}

function getWarehouses () {
  return axios.get(`${_apiBaseURLforOrderMgmt}/Meta/GetNtlWarehouses`, {
    ..._apiRequestHeader     
  })
  .then( response => response)
  .catch( error => error) 
}
function CreateReturnApi (returnOrder) {
  return axios.post(`${_apiBaseURLforOrderMgmt}/ReturnOrders/CreateReturnOrder`,{
      ...returnOrder
    },
{      ..._apiRequestHeader
}     )
    .then( response => response)
    .catch( error => error)
}

function getOrderDetails (orderNo) {
  return axios.get(`${_apiBaseURLforOrderMgmt}/ReturnOrders/GetReturnOrderDetails`, {
    params: {
      'returnorderId': orderNo
    },
    ..._apiRequestHeader     
  })
  .then( response => response)
  .catch( error => error) 
}
function approveReturnOrderApi (orderNo,comment) {
  return axios.post(`${_apiBaseURLforOrderMgmt}/ReturnOrders/ApproveReturnOrderRequest`, {
      'id': orderNo,
      'comments':comment     
  },
  {
    ..._apiRequestHeader
  }
  )
  .then( response => response)
  .catch( error => error) 
}
export {getCartonOrReelDetails,getOrderListingApi,getdownloadApi,getShippers,getWarehouses,CreateReturnApi,getOrderDetails,approveReturnOrderApi,
  getOrderReturns};